module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-skeleton-children',\n  template: `\n    <div class=\"article\">\n      <nz-skeleton [nzLoading]=\"loading\">\n        <h4>Ant Design, a design language</h4>\n        <p>We supply a series of design principles, practical patterns and high quality design resources (Sketch and Axure), to help people create their product prototypes beautifully and efficiently.</p>\n      </nz-skeleton>\n      <button nz-button (click)=\"showSkeleton()\" [disabled]=\"loading\">\n        Show Skeleton\n      </button>\n    </div>\n  `,\n  styles  : [\n    `\n      .article h4 {\n        margin-bottom: 16px;\n      }\n      .article button {\n        margin-top: 16px;\n      }\n    `\n  ]\n})\nexport class NzDemoSkeletonChildrenComponent {\n  loading = false;\n\n  showSkeleton(): void {\n    this.loading = true;\n    setTimeout(() => {\n      this.loading = false;\n    }, 3000);\n  }\n}\n"